#news_top_nav {
  background: @news-top;
  padding:20px 0;
  .news_top_nav {
    float:left;
    p {
      text-align: center;
    }
  }
  .news_top_nav:first-child {
    margin-top: 45px;
  }
  .news_title { 
    font-weight: bold;
    text-align: center;
  }
}

.nav_cetegory {
  margin: 10px 0;
  ul {
    list-style: none;
    padding-left: 0;
    li {
      display: inline;
      margin-left: 5px;
      a {
        text-decoration: underline;
        color: #cccccc;
      }
    }
    li:first-child {
      margin-left: 0;
    }
  }
}
.type_news {
  cursor: pointer;
}
#news_block_list {
  .news_block_list_title {
    font-size: 52px;
    font-weight: bold;
  }

}
.left_news {
  width: 55%;
  float: left;
}
.news_list {
  margin: 25px 0;
  .news_type {
    background: url("/news/img/strelka.png") left no-repeat;
    height: 50px;
    span {
      font-size: 18px;
      padding-top: 15px;
    }
  }
  .news {
    div:first-child {
      margin: 10px 0;
    }
    div:last-child {
      span {
        margin-left: 15px;
      }
      span:nth-child(1){
        margin-left: 0;
      }
      span:nth-child(even){
        margin-left: 0;
      }
    }
  }
}
#news_right_aside {
  div {
    .product_item {
      float: right;
      height: 170px;
    }
  }
}
.list_popular {
  float: right;
  width: 33%;
  .news {
    margin: 15px 0;
  }
  .news_title {
    font-size: 16px;
    margin: 3px;
  }
}
.form_comment {
  background: #F1F1F1;
  padding: 50px;
  div:first-child {
    label[for="email"] {
      margin-left: 10px;
    }
  }
  div:last-child {
    margin-top: 20px;
  }
  input[name="message"] {
      width: 100%;
      height: 200px;
  }
  input[type="submit"] {
    margin-top: 20px;
   }
}