#footer {
  background:@blue-ft;
  padding-bottom: 20px;
  .footer_top {
    padding: 40px 0;
    .footer_title  {
      color: @black;
      font-weight: @fw;
      line-height: 1.8;
      font-size:21px;
    }
    .footer_nav {
      margin-top: 20px;
      .nav_link {
        font-weight:@fw;
        text-decoration: underline;
        color:@black;
        font-size: 18px;
        line-height: 1.8;
      }

      ul>li>div {
        margin-top: @m;
      }
      ul>li>address {
        margin-top: 15px;
        margin-bottom: 10px;
      }
      ul>li>address:first-child {
        margin-top:15px;
      }
      #add-phone {
        font-size: 22px;
        font-weight: @fw;
      }
      ul>li>div>span {
        font-weight: @fw;
      }
    }
    .tp>ul>li {
      margin-top: 17px;
    }
    .tp>ul>li:first-child {
      margin-top: 0;
    }


    .footer_top>.container>.row>.col-lg-3 {
      margin-left: 20px;
    }
  }

}
.footer_top>div.container>div.row>div>nav>ul>li {
  list-style: none;
}
.news-list {
  list-style: none;
}
.social {
  margin: 0 auto;
  display: table;
  .f-social {
    margin: 0 auto;
    display: table;
  }
  .footer_link {
    font-weight:@fw;
    text-align:center;
  }
}
.social a:first-child {
  margin-left: 0px;
}
.social a{
  display: inline-block;
  vertical-align: middle;
  height: 31px;
  width: 31px;
  background: url(../../../images/social.png) no-repeat ;
  margin-left: 10px;
}
.social a.social_link_vk{
  background-position: -115px 0;
}
.social a.social_link_vk:hover{
  background-position: -115px -34px;
}
.social a.social_link_fb{
  background-position: -77px 0;
}
.social a.social_link_fb:hover{
  background-position: -77px -34px;
}
.social a.social_link_google{
  background-position: 0 0;
}
.social a.social_link_google:hover{
  background-position: 0 -34px;
}
.social a.social_link_youtube{
  background-position: -38px  0;
}
.social a.social_link_youtube:hover{
  background-position: -38px  -34px;
}


.modal_content p {
  font-size: 18px;
  color: white;
  padding: 60px;
  text-align: center;
  width: 100%;
}
.otzv_modal {
  background: #292C33;
  height: auto;
  position: fixed;
  top: 20%;
  left: 43%;
  z-index: 100;
  -webkit-transform: scale(0.9);
  -ms-transform: scale(0.9);
  transform: scale(0.9);
}
.successful_modal {
  width: 20% !important;
  height: 50% !important;
}
a.overlay-close {
  position: fixed;
  top: 5px;
  right: 10px;
  width: 48px;
  height: 48px;
  background: url("../img/cd-icon-close.svg") no-repeat center center;
  overflow: hidden;
  text-indent: 100%;
  white-space: nowrap;
  visibility: visible;
  opacity: 1;
  -webkit-transition: -webkit-transform 0.3s 0s, visibility 0s 0.4s;
  -moz-transition: -moz-transform 0.3s 0s, visibility 0s 0.4s;
  transition: transform 0.3s 0s, visibility 0s 0.4s;
  cursor: pointer;
}
.arrow_up_fix_none {
   background: url("/images/arrow_up.png") no-repeat;
   height: 80px;
   width: 80px;
   position: fixed;
   bottom: 2%;
   right: 2%;
   cursor: pointer;
   display: none;
  z-index: 200;
  -webkit-animation-name: arrow;
  -moz-animation-name: arrow;
  -o-animation-name: arrow;
  animation-name: arrow;
  animation-duration: 2s;
  animation-iteration-count: 1;
 }
.arrow_up_fix_block {
  background: url("/images/arrow_up.png") no-repeat;
  height: 80px;
  width: 80px;
  position: fixed;
  bottom: 2%;
  right: 2%;
  cursor: pointer;
  display: block;
  z-index: 200;
  -webkit-animation-name: arrow;
  -moz-animation-name: arrow;
  -o-animation-name: arrow;
  animation-name: arrow;
  animation-duration: 2s;
  animation-iteration-count: 1;
}
@keyframes arrow {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.overlay {
  background-color: rgba(0, 0, 0, 0.7);
  bottom: 0;
  cursor: default;
  left: 0;
  opacity: 0;
  position: fixed;
  right: 0;
  top: 0;
  display: none;
  z-index: 99999;
  -webkit-transition: opacity .5s;
  -moz-transition: opacity .5s;
  -ms-transition: opacity .5s;
  -o-transition: opacity .5s;
  transition: opacity .5s;
}