
/* SECTION */
section {
  padding: 60px 0;
  margin: 50px 0;
}
a {
  text-decoration: none;
  text-decoration: -moz-none;
}
p {
  font-size: 14px;
}
ul {
  list-style-type: disc;
  padding-left: 20px;
}
ol {
  list-style-type: decimal;
  padding-left: 20px;
}
.section-heading {
  font-size: 32px;
  text-align: center;
  margin: 40px 0 20px;
  line-height: @lh;
}
.product_title {
  text-align:center;
  font-size: 36px;
  font-weight: @fw;
  margin: 20px 0;
}
.news-list>li:nth-child(1) {
  margin-left:0px !important;
}
.news-list>li:first-child {
  margin-left:0px !important;
}
.product_item {
  background: url(../../../images/off_01.png) no-repeat;
  height: 150px;
  float: @fl;
  position: relative;
  .product_item_href {
    float: none !important;
    color: @red !important;
    padding: 0 !important;
    margin: 0 !important;
    -webkit-border-radius: 0px !important;
    -moz-border-radius: 0px !important;
    border-radius: 0px!important;
  }
  p {
    line-height: @lh;
  }
  .product_item_img {
    float:@fl;
  }
  .product_item_title {
    color: @red;
    font-weight: @fw;
    font-size: 20px;
    margin: 10px 0;
    line-height: 1;
  }

  .product_item_type a {
    float: @fr;
    background: @red;
    padding: 5px;
    text-decoration: none;
  .border-radius(@radius);
    margin:10px;
    margin-top: 5px;
    color: @white;
  }
  .product_item_type>a:last-child {
    position: absolute;
    right: 2%;
    bottom: 2%;
    padding: 5px 10px;
  }
}

#counter {
  background: url(../../../images/stock4.jpg) no-repeat bottom center;
  background-size: cover;

}
.dropdown-menu>li{
  margin:5px 12px !important;
}
.navbar-inverse {
  margin: 25px 0;
}

.counter-icon {
  display: block;
  font-size: 70px;
}

.counter {
  display: block;
  letter-spacing: 1px;
  font-size: 36px;
  font-family: "Montserrat", sans-serif;
  color: @white;
  text-align: center;
}

.counter-title {
  display: block;
  font-size: 24px;
  color: @white;
  text-align: center;
}
.urip-blue-color {
  color: #19a9e5;
}

.urip-orange-color {
  color: #f6a006;
}
p.section-title {
  color: #777;
  text-align: center;
  text-transform: uppercase;
  font-size: 16px;
  line-height: .9em;
}
p.section-title:after {
  display: block;
  margin: 0 auto;
  margin-top: 17px;
  width: 70px;
  height: 1px;
  background: #19a9e5;
  content: "";
}
.icon-gears:before {
  content: "\e07b";
}

.feature-icon {
  color: #777;
  font-size: 30px;
}
.the-feature {
  -webkit-transition: 0.2s;
  transition: 0.2s;
  margin-top: -10px;
  margin-bottom: 53px;
  padding: 15px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  border-radius: 8px;
  text-align: center;

  a {
    text-decoration: none !important;
  }
  .feature-title {
    margin-top: 17px;
    margin-bottom: 15px;
    font-size: 24px;
    color: #333;
    line-height: 1.5;
  }
  p {
    margin-bottom: 0;
    color: #777;
  }
}
.the-feature:hover {
  background: #F7F8F9;
  .feature-title {
    color: #19a9e5;
    a {
      text-decoration: none !important;
    }
  }
}
.centered {
  text-align: center;
}
a.contact-trigger {
  cursor: pointer;
}
a.contact-trigger:hover {
   text-decoration: none;
   color: @white;
 }
a.contact-trigger:focus {
  text-decoration: none;
  color: @white;
}
a.contact-trigger:active {
  text-decoration: none;
  color: @white;
}

.team-item-content {
  position: relative;
  overflow: hidden;
  margin-top: 10px;
}

.team-item-content img {
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.team-item:hover .team-item-content img {
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
}
.team-info {
  width: 270px;
  position: absolute;
  top: 85%;
  padding: 14px 20px 20px;
  background-color: rgba(34,34,34,.87);
  color: #fff;
  -webkit-transition: all 0.3s ease-out;
  transition: all .3s ease-in-out;
  -webkit-backface-visibility: hidden;
}

.team-item:hover .team-info {
  top: 0;
}

.team-info h6 {
  margin-top: 5px;
  margin-bottom: 30px;
  color: #fff;
}

.team-info h6:after {
  display: block;
  margin: 0 auto;
  margin-top: 6px;
  width: 30px;
  height: 1px;
  background: #fff;
  content: "";
}

.team-info small {
  font-size: 16px;
}
