}

.contact-form {
  margin: 30px 0;
}

.contact-form .contact-input,
.contact-form textarea,
.contact-form .submit-btn {
  position: relative;
  overflow: hidden;
  border: none;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  border-radius: 6px;
  background-clip: padding-box;
  box-shadow: none;
  color: #fff;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.contact-form .contact-input:focus,
.contact-form textarea:focus,
.contact-form .submit-btn:focus {
  outline: none;
  border: none;
}

.contact-form .contact-input:focus,
.contact-form textarea:focus {
  background: #515459;
}

.contact-form .contact-input {
  margin-bottom: 30px;
  padding: 0 20px;
  width: 100%;
  height: 50px;
  background: #42454a;
}

.contact-form textarea {
  margin-bottom: 24px;
  padding: 20px;
  width: 100%;
  background: #42454a;
}

.contact-form .submit-btn {
  min-width: 170px;
  height: 50px;
  background: #19a9e5;
  text-transform: uppercase;
  font-size: 20px;
}

.contact-form .submit-btn:hover {
  background: #11a1dd;
}



.required-field {
  position: relative;
  overflow: hidden;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  border-radius: 6px;
}

.required-field:after {
  position: absolute;
  top: -10px;
  right: -10px;
  display: block;
  width: 20px;
  height: 20px;
  background: #19a9e5;
  content: "";
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.required-field.field-error:after {
  background: #cc3530;
  /*
  box-shadow: inset 0 0 0 2px #cc3530;
  */
  color: #cc3530;
}

.submit-btn.disabled {
  -webkit-box-shadow: none;
  box-shadow: none;
  opacity: .65;
  filter: alpha(opacity=65);
  cursor: not-allowed;
  pointer-events: none;
}
.all-caps {
  font-size: 36px;
  text-transform: uppercase;
}
#what-we-do {
  position: relative;
}

/* 5.1 What We Do on Form Layout */
.hero-form-layout #what-we-do {
  padding-top: 30px;
}
/* -- */

#what-we-do:before {
  position: absolute;
  top: 10px;
  left: 0;
  width: 279px;
  height: 693px;
  background: url(../../../images/ipad-cover.png) no-repeat;
  content: "";
}

#what-we-do:after {
  position: absolute;
