.sout-btn {
  background: #0087C4 !important;

}
.sout-btn-red {
  font-size: 20px;
  float: right;
  color: white;
  margin: 25px 0;
  background: #FF3838;
  font-weight: 400;
  padding: 7px 20px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
}
.sout_display_block {
  display: block !important;
}
.sout-opisanie {
  width: 350px;
  float: left;
  margin-top: 20px;
  margin-left: 16px;
  padding: 20px;
  cursor: pointer;
  background: white;
  border: 3px solid #83AB16;
  min-height: 260px;
  span {
    display: block;
    height: 60px;
    font-weight: bold;
    font-size: 18px;
  }
  .sout_display_block {
    img {
      float: right;
      position: relative;
      left: 0px;
      top: 50px;
    }
  }
}
.sout_form {
  background: #292C33;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  border-radius: 15px;
  padding: 20px 0;
  h2,p {
    color: white !important;
  }
}
.sout_zav {
  width: 100%;
}
.sout_close {
  position: static !important;
  top: 0 !important;
  right: 0 !important;
  float: right !important;
}
.fz-zagolovok>h2 {
  font-size: 18px;
}
.soul_ul {
  list-style-type: none;
  .span_sout {
    float: left !important;
    margin-right: 7px;
  }
}