@media screen and (max-width: 992px) {
  #responsive-menu {
    margin: 0 auto;
    width: 100%;
  }
  .navbar-inverse {
    margin: 25px 0;
  }

  #our-team>.container {
    margin: 0;
  }
  .filter>li {
    float: left;
    margin-left: 10px;
  }
  .the-feature p {
    font-size: 16px;
  }
  #what-we-do:before, #what-we-do:after {
    display: none;
  }
  .footer_nav {
    margin-left: 50px;
  }
  .type_np {
    margin-left: 10px !important;
  }
  .nav {
    li {
      margin-left: 5px !important;
    }
  }
  .product_item {
    font-size: 12px;
    background: none;
    .product_item_title {
      font-size: 16px;
    }
    .product_item_type p {

    }
    .product_item_type a {
      margin-top: 5px !important;
    }
  }
  .product_item_img>img  {
    width: 70px;
    height: 70px;
  }
  .ac_popap {
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 318px;
    height: 252px;
    margin: auto;
  }

  .ac_popap .ac_popap_btn {
    position: relative;
    clear: both;
    z-index: 0;
    width: 116px;
    margin-right: 101px;
  }

  .ac_popap .ac_popap_close {
    margin-right: 13px;
  }

  .ac_popap .ac_popap_input {
    position: relative;
    z-index: 1;
    width: 248px;
    margin-top: 9px;
    margin-left: 35px;
  }

  .ac_popap .ac_popap_text {
    float: left;
    clear: both;
    margin-top: 14px;
    margin-right: 0;
    margin-left: 35px;
  }
  .left_news {
    width: 100%;
  }
  #news_top_nav {
    padding: 50px 0;
  }
}