.about_cat {
  float: left;
  text-align: center;
  margin-left: 15px;
  -webkit-filter: grayscale(100%);
  color: #909699;
  p,a {
    text-decoration: none;
    font-size: 18px;
  }
  .img_menu_about {
    height: 130px;
  }
}
.about_cat:hover {
  -webkit-filter: grayscale(0%);
  color: #0071BB ;
}
.active_about {
  -webkit-filter: grayscale(0%) !important;
  color: #0071BB !important;
}
#about_head {
  float: right;
}
.left_about {
  width: 65%;
  float: left;
}
#news_right_aside {

}
.ka_image {
  width: 375px;
}
.about_number {
  font-size: 44px !important;
  line-height: 1.5;
  color: #336799;
  float: left;
}
.about_type>span {
  padding: 0 !important;
  margin-left: 5px;
}
.about_type  {
  background: url("/images/short_arrow.png") left no-repeat !important;
  height: 65px !important;
  span:last-child {
    float: left !important;
    line-height: 3.5 !important;
    color: #336799;
  }
}
.long {
  background: url("/images/long_arrow.png") left no-repeat !important;
  height: 65px !important;
}
.td {
  text-decoration: underline;
}
.block_about {
  width: 280px;
  height: 345px;
  background: #C4E2F3;
  margin: 50px;
  -webkit-border-radius: 7px;
  -moz-border-radius: 7px;
  border-radius: 7px;
  float: right;
  img {
    float: right;
  }
   a {
     display: block;
     width: 200px;
     margin: 0 auto;
     color: black;
     border: 1.5px solid;
     -webkit-border-radius: 5px;
     -moz-border-radius: 5px;
     border-radius: 5px;;
     padding: 15px 55px 15px;
     text-decoration: none;
   }
  p {
    margin-left: 40px;
    font-size: 20px;
  }
}
.clients {
  img {
    float: left;
    margin-top: 50px;
    width: 216px;
  }
  div {
    float: left;
    width: 60%;
    margin-left: 20px;
    padding: 20px 0;
  }
}
.otzv-row {
  margin-top: 25px;
}
.otzv {
  float: left;
  div {
    float: left;
    width: 190px;
  }
  .otzvnamne {
    font-weight: bold;
  }
  div:first-child {
    img {
      display: block;
      margin: 0 auto;
    }
    span {
      text-align: center;
      display: block;
    }
  }
}
.vacancy {
  width: 100%;
  background: #C5E2F4;
  padding: 30px;
  cursor: pointer;
  margin: 30px 0;
  div {
    span {
      float: left;
      margin-left: 10px;
    }
    span:nth-child(even) {
      margin-left: 5px;
    }
    span:nth-child(1) {
      margin-left:0;
    }
  }
  .vacancy_hide {
    margin-top: 25px;
    display: none;
  }
  table>tbody>tr>td:nth-child(2) {
    padding-left: 50px;
  }
}

.otzv_modal {
  background: #292C33;
  height: auto;
  width: 50%;
  position: fixed;
  top:25%;
  left: 25%;
  display: none;
  z-index: 100;
   -webkit-transform: scale(0.9);
  -ms-transform: scale(0.9);
  transform: scale(0.9);
  .overlay-close {
    position: absolute;
    top:10px;
    right: 20px;
  }
}

.modal_content p {
  font-size: 18px;
  color: white;
  padding: 100px;
}

.table-responsive {
  font-size: 18px;
}
.inner_part {
  width: 70% !important;
  margin-top: 30px;

}
.put {
  float: left;
  width: 45%;
  margin-left: 20px;
}
.put:first-child {
  margin-left: 0;
}
.ankets_partner {
  text-align: center;
  margin: 20px 0;
  a {
    background-color: #4586C0;
    color: white;
    font-size: 22px;
    padding: 10px 30px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    border-radius: 10px;
    text-decoration: none;
  }
}
.conf {
  height: 180px;
  img {
    float: left;
  }
  p {
    float: left;
    color: #336699;
    font-size: 30px;
    width: 60%;
    margin-left: 10px;
    margin-top: 10px;
  }

}
.last_conf {
  text-align: center;
  font-weight: bold;
}
.conf_static {
  background: #E1F0F9;
  padding: 30px 0;
  h2 {
    text-align: center;
  }
  img {
    display: block;
    margin: 0 auto;
    width: 80%;
    height: auto;
  }
}
.conf {
  margin-top: 20px;
}
.conf:nth-child(4) {
  margin-top: 0;
}


