
@media screen and (max-width: 1200px) {
  .product_type_title {
    font-size: 32px !important;
  }
  #left-menu-ecp {
    margin-left: -40px;
  }

  #centr-ecp {
    width:100%;
  }
  .ecp-opisanie-two {
    border: 3px solid #B5CFE6 !important;
    width: 45%;

  }
  .type_np {
    margin-left: 3px;
  }

  .ecp-opisanie {
    float: left;
    margin-top: 20px;
    margin-left:20px;
    padding: 10px;
    cursor: pointer;
    background: white;
    border: 3px solid #83AB16;
    span {
      font-size: 16px;
      font-weight: bold;
      margin: 7px 0;
      display: block;
    }
  }
  #ecp-application-content-two {
    height: 329px !important;
    width: 770px !important;
    border-right: 2px solid #ccc;
  }
  #ecp-application-content-two ul:first-child {
    padding-left: 70px;
  }
  #ecp-application-content-two ul {

  }
  .list_popular {
    float: none;
  }
  .left_about {
    width: 100%;
  }
  #responsive-menu {
    margin: 0 auto;
    width: 80%;
  }
  .navbar-inverse {
    margin: 25px 0;
  }
  .nav {
    li {
      margin-left: 5px !important;
    }
  }
  .product_item {
    font-size: 12px;
    background: none;
    .product_item_title {
      font-size: 16px;
    }
    .product_item_type p {

    }
    .product_item_type a {
      margin-top:5px !important;
    }
  }
  .product_item_img>img {
    width: 70px;
    height: 70px;
  }
  .news_top_nav {
    img {
      width: 100px;
      display: block;
      margin: 0 auto;
    }
  }
}