#header {
  padding-top: 20px;
}
.logo {
  float:@fl;
}
#h-search-form:focus,:active {

}
header>ul, ol {
  list-style: none;
}
#h-search-form {
  max-width: 370px;
  position: relative;
  margin: 50px auto;
  input[type="text"] {
    height: 30px;
    width: 100%;
    border: 2px solid #afc2d3;
    padding-left: 7px;
    .border-radius(15px);
  }
  input[type="text"]:focus {
    outline:none;

  }
  input[type="submit"] {
    position: absolute;
    top: 0;
    right: 0;
    width: 24px;
    height: 25px;
    background: transparent url(../../../images/sprite.png) -26px -34px no-repeat;
    border:none;
    margin: 4px;
  }
  input[type="submit"]:focus {
    outline:none;
  }
  input[type="submit"]:hover {
    background: transparent url(../../../images/sprite.png) -50px -34px no-repeat;
  }
}

.h-contakt {
  float:@fr;
  text-align: center;
  font-weight:@fw;
  margin-top: 10px;
  .h-phone {
    line-height: @lh;
  }
  .h-phone:last-child {
    margin-top: 15px;
  }

}
.container-fluid {
  margin-top: 25px;
}
.navbar-toggle {
  border: none;
}
.navbar-toggle:hover,:focus, {
}
.nav {
  li {
      margin-left: 60px !important;
  }
}
.navbar-inverse {
  background:@blue;
  border:none;
  font-size: 18px;
}
.navbar-inverse .navbar-nav > li > a {
  color:@white;
}

#responsive-menu {
  margin: 0 auto;
}

.dropdown:hover > .dropdown-menu {
  display: block;
}
.navbar-nav > li > .dropdown-menu {
  margin-left: 12px;
}
.dropdown-menu {
  background: @blue;
  padding-top: 0px;
  border: none;
  li>a {
    color:@white;
    font-size: 16px;
  }

}
.dropdown-menu li li a:hover {
  background: black;
}
